// src/components/dashboard/PackagesSection.jsx
"use client";

import { useSession } from "next-auth/react";
import { loadStripe } from "@stripe/stripe-js";
import { useState }   from "react";
import PackageCard     from "../PackageCard";

export default function PackagesSection() {
  const { data: session } = useSession({ required: true });
  const userId = session.user.id;

  const pkgs = [
    { id:"1", name:"Paquete 1", sessions:4, price:1400, inscription:1000 },
    { id:"2", name:"Paquete 2", sessions:8, price:2250, inscription:1000 },
    { id:"3", name:"Paquete 3", sessions:12,price:2500, inscription:1000 },
  ];

  const buy = async pkgId => {
    const r = await fetch("/api/stripe/checkout", {
      method:"POST",
      headers:{"Content-Type":"application/json"},
      body: JSON.stringify({ pkgId, userId }),
    });
    const { id:sessionId } = await r.json();
    const stripe = await loadStripe(process.env.NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY);
    stripe.redirectToCheckout({ sessionId });
  };

  return (
    <div className="row g-4">
      {pkgs.map(p=>(
        <div className="col-md-4" key={p.id}>
          <PackageCard pkg={p} onBuy={()=>buy(p.id)} />
        </div>
      ))}
    </div>
  );
}
